<template>
	<view>
		
		<view class="words_result">
			<view class="item" v-for="(item,index) in words" :key="index">
				<text class="text">{{item.words}}</text>
				{{item.select}}
				<text class="copy" v-if="!select" @click="setClipboardData(item.words)">复制</text>
				<text class="select" v-else @click="checkItem(item)" :class="item.select?'active':''"></text>
			</view>
			
		</view>
		<view class="popup_bottom" v-if="!select">
			<button class="mini-btn" type="primary" size="mini" @click="allCopy">全部复制</button>
			<button class="mini-btn" type="primary" size="mini" @click="select=!select">选择复制</button>
		</view>
		<view class="popup_bottom" v-else>
			<button class="mini-btn" type="primary" size="mini" @click="selectCopy">复制</button>
			<button class="mini-btn" type="default" size="mini" @click="select=!select">取消</button>
		</view>
	</view>
</template>

<script>
	export default {
		props:{
			words_result:{
				type: Array,
				default: []
			}
		},
		mounted() {
			this.words = this.words_result
		},
		data() {
			return {
				select:false,
				words:[]
			};
		},
		methods:{
			checkItem(item){
				item.select= !item.select
				console.log(item.select)
			},
			allCopy(){
				this.setClipboardData(this.words_result.map(item=>item.words).join())
			},
			selectCopy(){
				this.setClipboardData(this.words_result.filter(item=>item.select).map(item=>item.words).join())
			},
			setClipboardData(data){
				wx.setClipboardData({
				  data: data,
				  success (res) {
				    wx.getClipboardData({
				      success (res) {
				        console.log(res.data) // data
				      }
				    })
				  }
				})
			}
		}
	}
</script>

<style lang="scss">
.words_result{
	margin-bottom: -2rpx;
	max-height: 75vh;
	overflow: auto;
	background: #fff;
	padding:0 20rpx 24rpx;
	text-align: center;
	.item{
		width: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 8rpx 0;
		border-bottom: 1rpx solid #cdcdcd;
		.text{
			font-size: 24rpx;
			color: #4CA2FF;
			text-align: left;
		}
		.copy{
			border: 1px solid #ff0000;
			border-radius: 12rpx;
			padding: 0 12rpx;
			font-size: 20rpx;
			height: 40rpx;
			line-height: 40rpx;
			color: #333;
		}
		.select{
			width: 20rpx;
			height: 20rpx;
			border-radius: 50%;
			border: 1px solid #3B4144;
			
		}
		.active{
			width: 20rpx;
			height: 20rpx;
			border-radius: 50%;
			border: 1px solid #ff0000;
			display: flex;
			align-items: center;
			justify-content: center;
		}
		.active::after{
			content: '';
			width: 12rpx;
			height: 12rpx;
			border-radius: 50%;
			background-color: #FF0000;
		}
	}
	.item:last-child{
		border-bottom:none;
	}
	
}
.popup_bottom{
	background: #fff;
	display: flex;
	justify-content: space-around;
	align-items: center;
	padding: 12rpx 0;
}
</style>
